
































import comment from '@/components/article_comment/comment.vue';
export default {
	components: {
		comment
	},
	data() {
		return {
			icon1: 0,
			articleView: {},
			commentList: [],
			isfault: false,
			articleId: ''
		}
	},
	onLoad(params) {
		this.articleId = params.id;
		this.getDetail();
	},
	methods: {
		share: () => {
			uni.share({
				provider: "weixin",
				scene: "WXSceneSession",
				type: 0,
				href: "http://uniapp.dcloud.io/",
				title: "辣妈母婴文章分享",
				summary: "我正在使用辣妈母婴APP，赶紧跟我一起来体验！",
				imageUrl: "../../static/images/logo.jpg",
				success: function(res) {
					console.log("success:" + JSON.stringify(res));
				},
				fail: function(err) {
					console.log("fail:" + JSON.stringify(err));
				}
			});
		},
		// 获取文章详情
		getDetail: function() {
			uni.showLoading({
				title: '加载中...'
			})
			uni.request({
				url: this.apiUrl + 'article/getArticle',
				method: 'GET',
				data: {
					id: this.articleId
				},
				success: res => {
					
					if (res.data.code == 0) {
						let content = res.data.data.content;
						res.data.data.content = content.replace(/\<img/gi, '<img style="width:100%;height:auto" ');
						this.articleView = res.data.data;
					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
		// 收藏
		heart() {
			this.icon1 = this.icon1 === 0 ? 1 : 0;
			if (this.icon1 === 1) {
				uni.showToast({
					title: "收藏成功",
					icon: "none"
				});
			} else {
				uni.showToast({
					title: "取消收藏",
					icon: "none"
				});
			}
		},
		// 去评论
		comment() {
			uni.navigateTo({ //跳转到评论页面
				url: '/pages/article/comment?articleId=' + this.articleId
			})
		}
	}
}
